
'use client';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Church, CheckCircle2 } from 'lucide-react';
import Link from 'next/link';

const signupFormSchema = z.object({
    name: z.string().min(2, { message: 'Name must be at least 2 characters.' }),
    email: z.string().email({ message: 'Please enter a valid email address.' }),
});

type SignupFormValues = z.infer<typeof signupFormSchema>;

export default function SignupPage({ params }: { params: { churchId: string } }) {
    const [isSubmitted, setIsSubmitted] = useState(false);

    const form = useForm<SignupFormValues>({
        resolver: zodResolver(signupFormSchema),
        defaultValues: {
            name: '',
            email: '',
        }
    });

    // In a real app, this would use `db.addVolunteer`
    const onSubmit = (data: SignupFormValues) => {
        console.log('New volunteer signup:', data, 'for churchId:', params.churchId);
        setIsSubmitted(true);
    };

    return (
         <main className="flex min-h-screen flex-col items-center justify-center p-8 bg-muted/40">
            <div className="text-center mb-8">
                <Church className="h-12 w-12 text-primary mx-auto mb-4" />
                <h1 className="text-4xl font-bold font-headline text-foreground tracking-tight sm:text-5xl">
                Join the Volunteer Team
                </h1>
                <p className="mt-3 text-lg text-foreground/80">
                    We're excited to have you!
                </p>
            </div>
            
            <Card className="w-full max-w-md">
                {isSubmitted ? (
                    <CardContent className="p-10 text-center flex flex-col items-center">
                        <CheckCircle2 className="h-16 w-16 text-green-500 mb-4" />
                        <h2 className="text-2xl font-semibold mb-2">Thank You!</h2>
                        <p className="text-muted-foreground mb-6">Your request to join has been sent to the administrator. You'll be notified once it's approved.</p>
                        <Button asChild>
                           <Link href="/">Return to Home</Link>
                        </Button>
                    </CardContent>
                ) : (
                    <>
                    <CardHeader>
                        <CardTitle>Sign Up</CardTitle>
                        <CardDescription>Enter your details below to get started.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <Form {...form}>
                            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
                                 <FormField
                                    control={form.control}
                                    name="name"
                                    render={({ field }) => (
                                        <FormItem>
                                        <FormLabel>Full Name</FormLabel>
                                        <FormControl>
                                            <Input placeholder="Jane Doe" {...field} />
                                        </FormControl>
                                        <FormMessage />
                                        </FormItem>
                                    )}
                                    />
                                <FormField
                                    control={form.control}
                                    name="email"
                                    render={({ field }) => (
                                        <FormItem>
                                        <FormLabel>Email Address</FormLabel>
                                        <FormControl>
                                            <Input placeholder="you@example.com" {...field} />
                                        </FormControl>
                                        <FormMessage />
                                        </FormItem>
                                    )}
                                    />
                                <Button type="submit" className="w-full mt-4">
                                    Request to Join
                                </Button>
                            </form>
                        </Form>
                    </CardContent>
                    </>
                )}
            </Card>
         </main>
    );
}
